blockquote {
    padding: 0;
    margin: 0;
    quotes: "\201c""\201d";
}

blockquote:before {
    content: open-quote;
}

blockquote:after {
    content: close-quote;
}

/* Override pdf.js styles ****************************************************/
.textLayer {
    z-index: -1;
}